@import url('https://fonts.googleapis.com/css2?family=Chakra+Petch:wght@700&display=swap');
.promptWrap .mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  z-index: 5;
}
.promptWrap .desktopBox,
.promptWrap .androidBox {
  background-color: #ffffff;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: 'Chakra Petch', serif;
  font-weight: 700;
  z-index: 6;
}
.promptWrap .desktopBox {
  height: 400px;
  border-radius: 20px;
  box-sizing: border-box;
  #padding: 0 20px;
  display: grid;
  justify-content: center;
  justify-items: center;
}
.promptWrap .desktopBox .topArea {
  padding: 24px 0 20px;
  border-bottom: 1px dashed #000000;
  align-items: center;
}
.promptWrap .desktopBox .bottomArea {
  font-size: 16px;
  line-height: 20px;
  padding: 20px 0 0;
}
.promptWrap .desktopBox .bottomArea strong {
  color: #7d59fe;
}
.promptWrap .desktopBox .logoHS {
  width: 56px;
  height: 73px;
  background: url(./img/logo_hs@3x.png) no-repeat;
  background-size: 56px 73px;
}
.promptWrap .desktopBox .logoTitle {
  font-size: 20px;
  line-height: 24px;
  margin-left: 16px;
}

.promptWrap .androidBox {
  width: 320px;
  height: 343px;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 0 20px;
}
.promptWrap .androidBox .bigLogo {
  width: 80px;
  height: 80px;
  background: url(./img/logo_big@3x.png) no-repeat;
  background-size: 80px 80px;
  position: absolute;
  top: -40px;
  left: 50%;
  transform: translateX(-50%);
}
.promptWrap .androidBox .topArea {
  padding: 58px 0 20px;
  border-bottom: 1px dashed #000000;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
}
.promptWrap .androidBox ol {
  list-style: none;
  padding: 0;
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
}
.promptWrap .androidBox ol li {
  margin-bottom: 18px;
  display: flex;
  align-items: flex-start;
}
.promptWrap .androidBox ol li b {
  font-size: 20px;
  position: relative;
  &::after {
    content: '';
    position: absolute;
    bottom: 1px;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #00ff88;
    z-index: 0;
  }
}
.promptWrap .androidBox ol li b span {
  position: relative;
  z-index: 1;
}
.promptWrap .androidBox ol li .text {
  margin-left: 6px;
  flex: 1;
}
.promptWrap .androidBox ol li strong {
  color: #7d59fe;
}
.promptWrap .androidBox ol li .bg {
  background: #f4f4f4;
  border-radius: 4px;
  padding: 2px 10px;
  margin: 0 10px;
}
.promptWrap .androidBox .icoShare {
  width: 24px;
  height: 24px;
  background: url(./img/ico_share@3x.png) no-repeat;
  background-size: 24px 24px;
  display: inline-block;
  vertical-align: middle;
  margin: 0 10px;
}
.promptWrap .androidBox .icoAdd {
  width: 16px;
  height: 16px;
  background: url(./img/ico_add@3x.png) no-repeat;
  background-size: 16px 16px;
  display: inline-block;
  vertical-align: -2px;
  margin-left: 5px;
}
.promptWrap .androidBox .icoSmallLogo {
  width: 32px;
  height: 32px;
  background: url(./img/logo_small@3x.png) no-repeat;
  background-size: 32px 32px;
  display: inline-block;
  margin: 0 10px;
  vertical-align: middle;
}

.promptWrap .desktopBox .desktopText{
  font-size: 1.5em;


}

.promptWrap .desktopBox .qrCode{
  width: 400px;
  height: 300px;
  background: url(./img/qr.jpg) no-repeat;
  background-position: center;
  #background-size: cover; /* 使背景图像覆盖整个容器 */
}